body {
    font-family: 'Open Sans', arial, sans-serif;
    overflow-y: auto;
    overflow-x: hidden;
  }
  
  * {
    margin: 0;
    padding: 0;
  }
  
  /** =======================================================================
                                      Hexes
     ========================================================================= */
  .hexes-container-right {
    position: absolute;
    top: -90px;
    right: -60px;
    width: 400px;
    height: auto;
    padding-top: 55px;
    padding-bottom: 55px;
    overflow: visible;
    z-index: 1;
  }
  .hexes-container-left {
    position: absolute;
    top: -90px;
    left: -60px;
    width: 400px;
    height: auto;
    padding-top: 55px;
    padding-bottom: 55px;
    overflow: visible;
    z-index: 1;
  }
  #categories {
    overflow: visible;
    width: 100%;
    margin: 0 auto;
    padding-bottom: 32.16760145166612%;
  }
  
  .clr:after {
    content: "";
    display: block;
    clear: both;
  }
  
  #categories li {
    position: relative;
    list-style-type: none;
    width: 27.85714285714286%;
    padding-bottom: 32.16760145166612%;
    float: left;
    overflow: hidden;
    visibility: hidden;
    -webkit-transform: rotate(-60deg) skewY(30deg);
    -ms-transform: rotate(-60deg) skewY(30deg);
    transform: rotate(-60deg) skewY(30deg);
    transition: transform 0.4s ease;
  }
  
  /** =======================================================================
                                      Calculations
     ========================================================================= */
  #categories li:nth-child(3n+2) {
    margin: 0 1%;
  }
  
  #categories li:nth-child(6n+4) {
    margin-left: 0.5%;
  }
  
  #categories li:nth-child(6n+4), #categories li:nth-child(6n+5), #categories li:nth-child(6n+6) {
    margin-top: -6.9285714285%;
    margin-bottom: -6.9285714285%;
    -webkit-transform: translateX(50%) rotate(-60deg) skewY(30deg);
    -ms-transform: translateX(50%) rotate(-60deg) skewY(30deg);
    transform: translateX(50%) rotate(-60deg) skewY(30deg);
    transition: transform 0.4s ease;
  }
 /* ========================================================================= */
    #categories li:hover {
    transform: rotate(-60deg) skewY(30deg) scale(1.07);
    }
    #categories li:nth-child(6n+4):hover,
    #categories li:nth-child(6n+5):hover,
    #categories li:nth-child(6n+6):hover {
    transform: translateX(50%) rotate(-60deg) skewY(30deg) scale(1.07);
    }
 /* ========================================================================= */
  #categories li:nth-child(6n+4):last-child, #categories li:nth-child(6n+5):last-child, #categories li:nth-child(6n+6):last-child {
    margin-bottom: 0%;
  }
  
  #categories li * {
    position: absolute;
    visibility: visible;
    background-color: #f1a406;
  }
  #categories li > div {
    width: 100%;
    height: 100%;
    text-align: center;
    color: #fff;
    overflow: hidden;
    -webkit-transform: skewY(-30deg) rotate(60deg);
    -ms-transform: skewY(-30deg) rotate(60deg);
    transform: skewY(-30deg) rotate(60deg);
    -webkit-backface-visibility: hidden;
  }
  
  /** =======================================================================
                                   Inside Content
     ========================================================================= */
  #categories li img {
    left: -100%;
    right: -100%;
    width: auto;
    height: 100%;
    margin: 0 auto;
  }
  
  #categories div h1, #categories div p {
    width: 90%;
    padding: 0 5%;
    background-color: #008080;
    background-color: rgba(0, 128, 128, 0.8);
    font-family: 'Raleway', sans-serif;
    -webkit-transition: top .2s ease-out, bottom .2s ease-out, .2s padding .2s ease-out;
    -ms-transition: top .2s ease-out, bottom .2s ease-out, .2s padding .2s ease-out;
    transition: top .2s ease-out, bottom .2s ease-out, .2s padding .2s ease-out;
  }
  
  #categories li h1 {
    bottom: 110%;
    font-style: italic;
    font-weight: normal;
    font-size: 1.5em;
    padding-top: 100%;
    padding-bottom: 100%;
  }
  
  #categories li h1:after {
    content: '';
    display: block;
    position: absolute;
    bottom: -1px;
    left: 45%;
    width: 10%;
    text-align: center;
    z-index: -1;
    border-bottom: 2px solid #fff;
  }
  
  #categories li p {
    padding-top: 50%;
    top: 110%;
    padding-bottom: 50%;
  }